body {
  background: rgb(11, 10, 13);
  background-image: initial;
  background-position-x: initial;
  background-position-y: initial;
  background-size: initial;
  background-repeat-x: initial;
  background-repeat-y: initial;
  background-attachment: initial;
  background-origin: initial;
  background-clip: initial;
  background-color: rgb(11, 10, 13);
}
.pagina {
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  background: url(https://skylab.rocketseat.com.br/static/a4edba24f9a571dbb09c831694960cdf.png)
      0% 0% / cover fixed,
    rgb(11, 10, 13);
}
.conteudo {
  padding: 30px;
  color: white;
}
.imagebtn {
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}
.imagem {
  width: 142px;
  height: 142px;
  margin-bottom: 15px;
  position: relative;
  border-width: 4px;
  border-style: solid;
  border-color: rgb(113, 89, 193);
  border-image: initial;
  border-radius: 50%;
}
.imagem:hover .imagem_camera {
  background: rgb(93, 77, 146);
}
.imagem_camera {
  cursor: pointer;
  right: 1px;
  top: 4px;
  font-size: 14px;
  position: absolute;
  width: 35px;
  height: 35px;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  overflow: hidden;
  border-radius: 50%;
  background: rgb(113, 89, 193);
}
.imagem img {
  width: 134px;
  height: 134px;
  opacity: 1;
  border-radius: 50%;
}
input[type="file"] {
  display: block;
  font-size: 999px;
  width: 134px;
  height: 134px;
  opacity: 0;
  position: absolute;
  text-align: right;
  z-index: 555;
}
.links {
  display: flex;
  flex-direction: row;
  -webkit-box-pack: center;
  justify-content: center;
  margin-top: 20px;
}
.links a,
.logout {
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: start;
  justify-content: flex-start;
  margin-left: 5px;
  margin-bottom: 10px;
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: bold;
  padding: 23px;

  border-radius: 5px;
  background: rgb(25, 24, 31);
}
.links p {
  display: block;
  margin-block-start: 0.6em;
}
.links svg {
  order: -1;
}
.links a:hover {
  color: rgb(113, 89, 193);
}
.logout {
  color: rgb(211, 66, 66);
}
form,
button {
  border: none;
  margin: 0px;
}
@media screen and (max-width: 767px) {
  .links {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
  }
  .logout {
    margin-left: -20px;
    padding: 20px 45px;
  }
  .conteudo {
    padding: 0px;
    color: white;
  }
}
