@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.text_center {
  color: #fff;
}

#entrar,
#entrarg {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 16px;
  border: transparent;
  height: 50px;
  font-weight: 900;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
}
#entrar,
#entrarg:focus {
  outline: none;
}
p {
  cursor: pointer;
}
.all {
  width: 100vw;
  height: 100vh;
  background: #fff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: url(https://skylab.rocketseat.com.br/static/7c60c57e4fd20119d82b5970028b2d1b.svg)
      center bottom repeat-x,
    url(https://skylab.rocketseat.com.br/static/d963b8944752859b3c6ccb0bda539c46.svg)
      repeat-x,
    rgb(11, 10, 13);
  background-image: url(https://skylab.rocketseat.com.br/static/7c60c57….svg),
    url(https://skylab.rocketseat.com.br/static/d963b89….svg), initial;
}
@media (min-width: 375px) and (max-width: 420px) {
  #icone {
    height: 17vh;
    width: 17vw;
    position: absolute;
    top: -7vw;
    left: 24vw;
  }
}

@media (max-width: 359px) and (min-width: 200px) {
  label#inpt1,
  label#inpt2 {
    display: inline-block;
    margin-bottom: 10px;
    background: #18181f;
    padding: 14px;
    border-radius: 5px;
    width: 28vw;
    border: 2px solid #28272c;
    transition: all 0.15s ease-in-out;
  }
  input {
    margin-left: -3px;
  }
  div.box {
    margin-left: 0vw !important;
  }
  #icone {
    margin-top: -20px;
  }
}
@media (max-width: 700px) {
  img#icone {
    height: 55vh;
    width: 55vw;
    position: absolute;
    top: -35vw;
    right: 16vh;
  }
  div.box {
    width: 100vw;
    height: 100vh;
    padding-left: 7.5vw;
  }
  form {
    margin-left: 0px !important;
  }
  label#inpt1,
  label#inpt2 {
    width: 85vw !important;
    padding: 10px !important;
  }
  #entrar,
  #entrarg {
    width: 85vw !important;
    margin-top: 20px;
    margin-bottom: 16px;
    border: transparent;
    height: 50px;
    font-weight: 900;
    font-size: 16px;
    color: #fff !important;
  }
  div.linha {
    margin-right: 50px !important;
  }
  input {
    font-size: 16px;
  }
}

@media (max-width: 768px) and (min-width: 701px) {
  img#icone {
    height: 55vh;
    width: 55vw;
    position: absolute;
    top: -25vw;
    right: 20vh;
  }
  div.box {
    width: 100vw;
    height: 100vh;
    padding-left: 7.5vw;
  }
  form {
    margin-left: 0px !important;
  }
  label#inpt1,
  label#inpt2 {
    width: 85vw !important;
  }
  #entrar,
  #entrarg {
    width: 85vw !important;
    margin-top: 20px;
    margin-bottom: 16px;
    border: transparent;
    height: 50px;
    font-weight: 900;
    font-size: 16px;
  }
  div.linha {
    margin-right: 120px !important;
  }
  .text_center {
    margin-right: 100px;
  }
}
@media (max-width: 1024px) and (min-width: 769px) {
  label#inpt1,
  label#inpt2 {
    display: inline-block;
    margin-bottom: 10px;
    background: #18181f;
    padding: 14px;
    border-radius: 5px;
    width: 28vw;
    border: 2px solid #28272c;
    transition: all 0.15s ease-in-out;
  }
  input {
    margin-left: -3px;
  }
  div.box {
    padding-left: 0.5vw;
  }
}

.box {
  width: 28vw;
  height: 70vh;
  position: absolute;
  top: 12vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
form {
  margin-left: 14px;
}

#icone {
  height: 17vh;
  width: 17vw;
  position: absolute;
  top: -9vw;

  /*animation: animacao 2.2s ease-in-out infinite;*/
}
span#iptico {
  margin-left: 0px !important;
  margin: 10px;
  padding-bottom: 10px;
}
label#inpt1,
label#inpt2 {
  display: inline-block;
  margin-bottom: 10px;
  background: #18181f;
  padding: 14px;
  border-radius: 5px;
  width: 28vw;
  border: 2px solid #28272c;
  transition: all 0.15s ease-in-out;
}
label#inpt1:focus-within,
label#inpt2:focus-within {
  text-decoration: none;
  transition: all 0.15s ease-in-out;
  border: 2px solid #7c62d4;
}

input:focus {
  outline: none;
}
input {
  background: transparent;
  border: transparent;
  color: white;
  width: 90%;
  font-size: 15px;
}

#entrar {
  background: rgb(124, 98, 212);
  margin-top: 20px;
  margin-bottom: 16px;
  border: transparent;
  height: 50px;
  font-weight: 580;
  font-size: 16px;
  color: white;
}
#entrarg {
  background: #d46262;
  margin-top: 20px;
  margin-bottom: 16px;
  border: transparent;
  height: 50px;
  font-weight: 580;
  font-size: 16px;
}

p#colorido {
  margin-top: 10px !important;
}
#colorido {
  color: #7c62d4 !important;
  font-weight: 500;
}
.text_center {
  margin-top: 25px;
}
div.linha::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0px;
  width: calc(50% - 25px);
  height: 1px;
  background: rgb(40, 39, 47);
}
div.linha::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0px;
  width: calc(50% - 25px);
  height: 1px;
  background: rgb(40, 39, 47);
}
div.linha {
  position: relative;
  font-size: 12px;
  color: rgb(135, 134, 139);
  text-align: center;
  margin: 24px 0px;
}

@keyframes animacao {
  0% {
    margin-top: 0vw;
    /*box-shadow: 0 20px 100px 0 rgba(124, 98, 212, 0.7);*/
    -webkit-filter: drop-shadow(0px 20px 100px rgba(124, 98, 212, 0.7));
    filter: drop-shadow(0px 20px 100px rgba(124, 98, 212, 0.7));
  }
  50% {
    margin-top: 0.6vw;
    z-index: 10px;
    /*box-shadow: 0 20px 20px 0 rgba(124, 98, 212, 0.7);*/
    -webkit-filter: drop-shadow(0px 20px 100px rgba(124, 98, 212, 0.7));
    filter: drop-shadow(0px 20px 20px rgba(124, 98, 212, 0.7));
  }
  100% {
    margin-top: 0vw;
    /*box-shadow: 0 20px 100px 0 rgba(124, 98, 212, 0.7);*/
    -webkit-filter: drop-shadow(0px 20px 100px rgba(124, 98, 212, 0.7));
    filter: drop-shadow(0px 20px 100px rgba(124, 98, 212, 0.7));
  }
}
